

@font-face{
   font-family: 'JetBrainsMono-Regular';
   font-weight: 500;
   font-style: normal;
   src: url(/guests-number/static/media/JetBrainsMono-Regular.0033a19b.woff2) format('woff2'), url(/guests-number/static/media/JetBrainsMono-Regular.7c41c98a.woff) format('woff');
}
.add-guests-main{
   font-family: 'JetBrainsMono-Regular';
}

.container-house{
   box-sizing: border-box;   
   width: 500px;
   height: 200px;
   border: 1px solid black;
   -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
           filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
   border-radius: 6px;
   display: flex;
   flex-direction: column;
   margin-bottom: 15px;
   overflow: scroll;
}

.button-house{
   margin: 0 auto;
   background-color: #EDF6FF;
   width: 180px;
   height: 40px;
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
   display: grid;
   grid-template-columns: 0.25fr 0.75fr;
}

.img-button{
   width: 30px;
   height: 30px;
   margin: auto;
}

.description-button{
   display: flex;
   justify-content: center;
   align-items: center;
}

.button-house:hover{
   margin-top: 2px;
   cursor: pointer;
   background-color: #C8DCEE;
   box-shadow: 0px 7px 8px rgba(0, 0, 0, 0.25);
}

.transition{
   transition: 0.4s;
}

.container-buttons-add-delete{
   display: grid;
   grid-template-columns: 1fr 1fr;
   max-width: 500px;
}

.house-number{
   margin: 5px auto;
   font-size: 24px;
}

.container-buttons-add-addult-child{
   display: flex;
   flex-direction: column;
   grid-gap: 10px;
   gap: 10px;
}

.container-house::-webkit-scrollbar {
   width: 0;
}

.list-adults-body{
   margin: 0 auto;
   width: 180px;
   height: 1600px;
   display: flex;
   flex-direction: column;
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.adult-count{
   width: 100%;
   height: 40px;
   background-color: #FBFDFF;
   display: flex;
   justify-content: center;
   align-items: center;
}

.adult-count:hover{
   background-color: #78B0E8;
   cursor: pointer;
}

.list-children-body{
   margin: 0 auto;
   width: 180px;
   height: 680px;
   display: flex;
   flex-direction: column;
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.description-button-child{
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 14px;
}

.button-house-child{
   margin: 0 auto;
   background-color: #EDF6FF;
   width: 180px;
   height: 40px;
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
   display: grid;
   grid-template-columns: 0.25fr 0.5fr 0.25fr;
}

.delete-child{
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 24px;
}

.button-house-child:hover{
   cursor: pointer;
   background-color: #C8DCEE;
   box-shadow: 0px 7px 8px rgba(0, 0, 0, 0.25);
}



